<template>
  <v-row>
    <v-container class="aparece2" id="containerAbout">
      <v-row class="justify-end">
        <v-col cols="12" class="p-absolute i-1">
          <p class="fs-200 alejandroperez-black apgrey pl-5" id="animado">SOBRE MI</p>
        </v-col>
        <v-col cols="8" class="i-99 subtext mr-5">
          <div class="text-end w-90">
            <img src="../../assets/profile/profile.jpg" class="aparece3" id="img-profile">
            <p class="fs-36 alejandroperez-light text-left text-md-justify">
              Soy un apasionado del front-end, <b>graduado en informática</b> y con unas ganas increíbles de desarrollar ideas.
              <br/><br/>
              He estado escribiendo código durante 3 años. En este tiempo trabajé para lugares como <b>California, México, Panamá, Chile, Venezuela</b> y como resultado aprendí muchas cosas.
            </p>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </v-row>
</template>

<script>
  
  export default{
    name: 'about',

    methods:{
      apareceScroll(clase,animation){
        var html2 = document.getElementsByTagName('html')[0];
        var elementAparece2 = document.getElementsByClassName(clase);

        document.addEventListener("scroll",function(){
          var topVent2 = html2.scrollTop;
          for(let i = 0; i < elementAparece2.length; i++){
            var toplemAparece2 = elementAparece2[i].offsetTop;
            if(topVent2 > toplemAparece2 - 550){
              elementAparece2[i].setAttribute('class',`animate__animated animate__${animation}`);
            }
          }
        })

      },

    },

    mounted(){
      if( window.innerWidth >= 960 ){ /* Desktop version */
        this.apareceScroll('aparece2','fadeInRight');
        this.apareceScroll('aparece3','swing animate__delay-1s');

        let elemento = document.getElementById("containerAbout");
        elemento.className += " oculto";
      }
    }

  }
</script>