<template>
  <v-row class="text-center justify-end">
      <v-container>
        <v-row class="text-center justify-end">
          <v-col cols="10" class="p-absolute i-1" v-if="version == 'desktop'">
            <p class="fs-200 alejandroperez-black apgrey" id="contact">CONTÁCTAME</p>
          </v-col>
          <v-col cols="12" md="10" id="contactForm">
            <p class="fs-100 alejandroperez-black apgrey" v-show="version == 'mobile'">CONTÁCTAME</p>

            <v-row class="justify-center mt-5">
              <v-col>
                <modal icon="mdi-email" title="Email:" description="alejandroperez.programador@gmail.com"></modal>
              </v-col>
              <v-col>
                <modal icon="mdi-linkedin" title="Linkedin:" description=""></modal>
              </v-col>
              <v-col>
                <modal icon="mdi-instagram" title="Instagram:" description=""></modal>
              </v-col>
            </v-row>

          </v-col>
        </v-row>
      </v-container>
    </v-row>
</template>

<script>

  import modal from '@/components/Modal.vue'

  export default{
    name: 'contact',

    components:{
      modal
    },

    data () {
      return {
        dialog: false,
      }
    },

    created(){
      ( window.innerWidth <= 768 )?this.version = 'mobile':this.version = 'desktop';
    },

    mounted(){

      if( window.innerWidth >= 768 ){
        let form = document.getElementById('contactForm');
        form.classList.add('i-99');
        form.classList.add('subtext');
      }
      
    }
    
  }
</script>