<template>
  <v-row class="totalVH align-center aparece oculto" style="margin: 5rem 0;">

      <!-- mobile version -->
      <v-container v-if="version == 'mobile'">
        <v-row>
          <v-col cols="12" class="d-flex align-center image-container sucess">
            <img :src="getImgUrl(nameImg)" class="imageWork">
          </v-col>
          <v-col cols="12">
            <b class="fs-30 alejandroperez-heavy">{{ number }}</b>

            <div class="work-description">
              <p class="fs-80 alejandroperez-heavy">{{ business }}</p>
              <p class="fs-30 alejandroperez-medium">{{ country }}</p>
              <p class="fs-30 alejandroperez-book">{{ description }}</p>
            </div>

            <b class="fs-30 alejandroperez-heavy">{{ type }}</b>
          </v-col>
        </v-row>
      </v-container>

      <!-- desktop version -->
      <v-container v-if="version == 'desktop'">
        <v-row>
          <v-col cols="6">
            <b class="fs-30 alejandroperez-heavy">{{ number }}</b>

            <div class="work-description">
              <p class="fs-80 alejandroperez-heavy">{{ business }}</p>
              <p class="fs-30 alejandroperez-medium">{{ country }}</p>
              <p class="fs-30 alejandroperez-book">{{ description }}</p>
            </div>

            <b class="fs-30 alejandroperez-heavy">{{ type }}</b>
          </v-col>
          <v-col cols="6" class="d-flex align-center image-container sucess">
            <img :src="getImgUrl(nameImg)" class="imageWork">
          </v-col>
        </v-row>
      </v-container>
  </v-row>

</template>

<style>
  
  .imageWork{
    width: 100%;
    height: 100%;
  }

</style>

<script>
  
  export default{
    props:{
      number: String,
      business: String,
      country: String,
      type: String,
      description: String,
      nameImg: String
    },

    data(){
      return{
        version: ''
      }
    },

    created(){
      ( window.innerWidth <= 768 )?this.version = 'mobile':this.version = 'desktop';
    },

    methods:{

      getImgUrl(name) {
        let images = require.context('../assets/works/', false, /\.png$/)
        return images('./' + name + ".png")
      }

    }
  }

</script>