<template>
  
  <v-dialog v-model="dialog" width="500">
    <template v-slot:activator="{ on, attrs }">
      <v-icon class="icon" v-bind="attrs" v-on="on">{{ icon }}</v-icon>
    </template>

    <v-card>
      <v-card-title class="text-h5 grey lighten-2">
        {{ title }}
      </v-card-title>

      <v-card-text class="py-5">
        {{ description }}
        <div v-if="icon == 'mdi-linkedin'" class="badge-base LI-profile-badge" data-locale="es_ES" data-size="medium" data-theme="dark" data-type="VERTICAL" data-vanity="alejandro-perez-programador-front-end" data-version="v1">
          <a class="badge-base__link LI-simple-link" href="https://ve.linkedin.com/in/alejandro-perez-programador-front-end?trk=profile-badge" target="_blank">Ver perfil de linkedin</a>
        </div>
        <div v-if="icon == 'mdi-instagram'" class="badge-base LI-profile-badge" data-locale="es_ES" data-size="medium" data-theme="dark" data-type="VERTICAL" data-vanity="alejandro-perez-programador-front-end" data-version="v1">
          <a class="badge-base__link LI-simple-link" href="https://www.instagram.com/alejandro.perez.frontend/" target="_blank">Ver perfil de instagram</a>
        </div>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" text @click="dialog = false">
          Cerrar
        </v-btn>
      </v-card-actions>

    </v-card>
  </v-dialog>

</template>


<script>
  
 export default{
  name: 'modal',

  props:{
    icon: String,
    title: String,
    description: String
  },

  data () {
    return {
      dialog: false,
    }
  },

 }

</script>