<template>
  <div>

    <workPortfolio number="01" business="Baredu" country="Venezuela" type="WEB APP" 
                   description="Centro de reservaciones para hoteles y excursiones, agencias de viajes, boletería nacional y servicios turísticos."
                   nameImg="baredu">
    </workPortfolio>

    <workPortfolio number="02" business="Barates" country="Venezuela" type="WEB APP" 
                   description="Sistema administrativo para el manejo de hoteles, reservaciones y paquetes turísticos de baredu viajes."
                   nameImg="barates">
    </workPortfolio>

    <workPortfolio number="03" business="Mejora solutions" country="México" type="WEB PAGE" 
                   description="Empresa especializada en Consultoría, Auditorías y Formación en diversos Sistemas de Gestión."
                   nameImg="mejora-solutions">
    </workPortfolio>

    <workPortfolio number="04" business="BFG" country="California" type="WEB PAGE" 
                   description="Belvedere financial group es una empresa dedicada a bienes raíces."
                   nameImg="bfg">
    </workPortfolio>

  </div>
</template>

<script>

  import workPortfolio from '@/components/Work.vue'
  
  export default{
    name: 'works',

    components:{
      workPortfolio
    },

    methods:{
      apareceScroll(){
        var html = document.getElementsByTagName('html')[0];
        var elementAparece = document.getElementsByClassName('aparece');

        document.addEventListener("scroll",function(){
          var topVent = html.scrollTop;
          for(let i = 0; i < elementAparece.length; i++){
            var toplemAparece = elementAparece[i].offsetTop;
            if(topVent > toplemAparece - 500){
              elementAparece[i].setAttribute('class','animate__animated animate__fadeInRight');
            }
          }
        })

      }
    },

    mounted(){
      this.apareceScroll();
    }
  }

</script>