<template>
  <v-col cols="4">

    <!-- mobile version -->
    <v-row class="align-center skills rounded-pill mx-1 text-center" v-if="version == 'mobile'">
      <v-col cols="12" class="pb-0">
        <img :src="getImgUrl(imgName)" style="width: 50%" class="rounded-circle">
      </v-col>
      <v-col cols="12" class="pt-0">
        <b class="fs-10">{{ lenguaje }}</b>
      </v-col>
    </v-row>

    <!-- desktop version -->
    <v-row class="align-center skills rounded-pill mx-1" v-if="version == 'desktop'">
      <v-col cols="3">
        <img :src="getImgUrl(imgName)" style="width: 90%" class="rounded-circle">
      </v-col>
      <v-col cols="9">
        <b>{{ lenguaje }}</b>
        <p>{{ description }}</p>
      </v-col>
    </v-row>


  </v-col>
</template>

<script>
  
  export default{
    name: 'skill',

    props:{
      imgName: String,
      lenguaje: String,
      description: String
    },

    data(){
      return{
        version: ''
      }
    },

    created(){
      ( window.innerWidth <= 768 )?this.version = 'mobile':this.version = 'desktop';
    },

    methods:{
      getImgUrl(name) {
        let images = require.context('../assets/lenguajes-icons/', false, /\.png$/)
        return images('./' + name + ".png")
      }
    }

  }

</script>